/* Colors */
.SearchField {
  position: relative;
  margin-bottom: 20px; }
  .SearchField.-home .searchfield {
    height: 70px;
    width: 90%;
    font-size: 30px;
    border-radius: 5px;
    padding: 10px 70px 10px 20px; }
  .SearchField.-home .button {
    height: 70px;
    width: 60px;
    right: 10%; }
  .SearchField .searchfield {
    width: 100%;
    min-height: 50px;
    padding: 10px;
    background-color: #D7D5CE;
    border: none;
    border-radius: 0;
    font-size: 16px; }
  .SearchField .button {
    padding: 18px 18px;
    width: 25px;
    position: absolute;
    right: 10px;
    background-repeat: no-repeat;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background-color: transparent;
    background-position: center;
    color: transparent;
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    cursor: pointer; }



